                                                        </div>
                                                        <hr />
                                                        <div class="grid align-items-center">
                                                            <div class="col">
                                                                Fecha Pedido: 
                                                            </div>
                                                            <div class="col font-bold">{{ item.fecha_pedido }}</div>
                                                        </div>
                                                        <hr />
                                                        <div class="grid align-items-center">
                                                            <div class="col">
                                                                Forma Envio: 
                                                            </div>
                                                            <div class="col font-bold">{{ item.forma_envio }}</div>
                                                        </div>
                                                        <hr />
                                                        <div class="grid align-items-center">
                                                            <div class="col">
