                                                            <div class="col font-bold">{{ item.cliente }}</div>
                                                        </div>
                                                        <hr />
                                                        <div class="grid align-items-center">
                                                            <div class="col">
                                                                Producto: 
                                                            </div>
                                                            <div class="col font-bold">{{ item.producto }}</div>
                                                        </div>
                                                        <hr />
                                                        <div class="grid align-items-center">
                                                            <div class="col">
