                                                        <hr />
                                                        <div class="grid align-items-center">
                                                            <div class="col">
                                                                Subcategoria: 
                                                            </div>
                                                            <div class="col font-bold">{{ item.subcategoria }}</div>
                                                        </div>
                                                        <hr />
                                                        <div class="grid align-items-center">
                                                            <div class="col">
                                                                Cantidad: 
                                                            </div>
                                                            <div class="col font-bold">{{ item.cantidad }}</div>
                                                        </div>
                                                        <hr />
                                                        <div class="grid align-items-center">
                                                            <div class="col">
                                                                Descuento: 
                                                            </div>
                                                            <div class="col font-bold">{{ item.descuento }}</div>
                                                        </div>
                                                        <hr />
                                                        <div class="grid align-items-center">
                                                            <div class="col">
